.link {
  font-size: 2vh;
  font-weight: 400;
  line-height: 5vh;
  width: 10vw;
  text-align: center;
  margin: 20px;
  margin-left: 2vw;
  transition: all 0.5s ease-in-out;
  box-shadow: 0.8vh 0.8vh 0vh -0.5vh #FCC700;
}



/* mouse over link */
.link:hover {
  text-decoration: none;
  color: #FCC700;
}

.link__active{
  text-decoration: none;
  color: #FCC700;
  box-shadow: -0.8vh -0.8vh 0vh -0.5vh #FCC700;
}

@-webkit-keyframes hide {
  from {box-shadow: -0.8vh -0.8vh 0vh -0.5vh #FCC700}
  to {  box-shadow: 0.8vh 0.8vh 0vh -0.5vh #FCC700}
}

@keyframes hide {
  from {box-shadow: -0.8vh -0.8vh 0vh -0.5vh #FCC700}
  to {  box-shadow: 0.8vh 0.8vh 0vh -0.5vh #FCC700}
}

@-webkit-keyframes show {
  from {box-shadow:  0.8vh 0.8vh 0vh -0.5vh #FCC700}
  to {  box-shadow: -0.8vh -0.8vh 0vh -0.5vh #FCC700}
}

@keyframes show {
  from {box-shadow:  0.8vh 0.8vh 0vh -0.5vh #FCC700}
  to {  box-shadow: -0.8vh -0.8vh 0vh -0.5vh #FCC700}
}

.link__hide{
  font-size: 2vh;
  font-weight: 400;
  line-height: 5vh;
  width: 10vw;
  text-align: center;
  margin: 20px;
  margin-left: 2vw;
  -webkit-animation-name: hide;
          animation-name: hide;
  -webkit-animation-duration: 0.5s;
          animation-duration: 0.5s;
  transition: all 0.5s ease-in-out;
  box-shadow: 0.8vh 0.8vh 0vh -0.5vh #FCC700;
}

.link__show{
  font-size: 2vh;
  font-weight: 400;
  line-height: 5vh;
  width: 10vw;
  text-align: center;
  margin: 20px;
  margin-left: 2vw;
  -webkit-animation-name: show;
          animation-name: show;
  -webkit-animation-duration: 0.5s;
          animation-duration: 0.5s;
  transition: all 0.5s ease-in-out;
  box-shadow: -0.8vh -0.8vh 0vh -0.5vh #FCC700;
  color: #FCC700;
}




.Slider {
    position: relative;
    height: 60vh;
    width: 100%;
    background-size: 100% 100%;
    border-top: solid 3vh #FCC700;
    border-bottom: solid 3vh #FCC700;
    overflow: hidden;
}

.Slider__go__left {
    transform: translate(0, 0);
    transition: all 1s;
}

.Slider__go__right {
    transform: translate(-200vw, 0);
    transition: all 1s;
}

.Slider__elements {
    position: absolute;
    width: 300%;
    height: 100%;

}

.Slider__transform {
    transform: translate(-100vw, 0);
}

.Slider__element {
    position: absolute;
    height: 100%;
    width: 100vw;
    background-size: 100% 100%;
    background-repeat: no-repeat;
}

.Slider__left {
    left: 0%;
}

.Slider__center {
    left: 100vw;
}

.Slider__right {
    left: 200vw;
}




.Slider__box {
    position: absolute;
    top: 25%;
    left: 20%;
    width: 25%;
    height: 40%;
    background-color: #E5E5E5;
    opacity: 0.9;
}

.Slider__box h2 {
    font-size: 4vh;
    margin: 10px;
    color: #14213D;
}

.Slider__box p {
    font-size: 2vh;
    margin: 10px;
    opacity: 1;
}

.Slider__Arrow {
    position: absolute;
    top: 50%;
    transform: translateY(-50%);
    opacity: 0.7;
    cursor: pointer;
    transition: all 0.5s ease-in-out;
    z-index: 3;
}

.Slider__Arrow:hover {
    opacity: 1;
}

.prevArrow {
    left: 0;
}

.nextArrow {
    right: 0;
}
.aboutColumn {
    width: 25%;
    height: 60%;
    text-align: center;
    border-bottom: 3px solid black;
}

.aboutColumn__icon {
    position: absolute;
    margin-left: -2%;
    margin-top: -2%;
    transform: rotate(-25deg);
    transition: all 0.5s ease-in-out;
}

.aboutColumn__icon img {
    width: 80%;
    height: auto;
}

.aboutColumn__icon:hover {
    transform: rotate(0deg);
}


.aboutColumn__title {
    width: 100%;
