    background-color: #FCC700;
    height: 6vh;
    z-index: -1;
    box-shadow: 10px 10px 0px -3px rgba(0, 0, 0);
}

.aboutColumn__title::before {
    position: absolute;
    content: "";
    height: 8vh;
    width: 6vw;
    z-index: 0;
}

.aboutColumn__title h2 {
    padding-top: 1.2vh;
